.button {
	border: none;
	cursor: pointer;
	outline: none;
	border-radius: var(--common-border-radius);
	transition: var(--common-transition);
}

.button_type_primary {
	color: var(--text-primary-color);
	background: linear-gradient(63.18deg, #801ab3 0%, #4c4cff 100%);
}

.button_type_primary:hover {
	box-shadow: 0px 0px 16px 8px rgba(51, 51, 255, 0.25), 0px 0px 8px 8px rgba(51, 51, 255, 0.25);
	-webkit-filter: drop-shadow(0px 4px 32px rgba(51, 51, 255, 0.5));
	        filter: drop-shadow(0px 4px 32px rgba(51, 51, 255, 0.5));
}

.button_type_primary:active {
	opacity: 0.9;
	/*background: linear-gradient(0deg, rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), linear-gradient(63.18deg, #801AB3 0%, #4C4CFF 100%);*/
}

.button_type_secondary {
	color: var(--colors-interface-accent);
	background: transparent;
}

.button_type_secondary:hover {
	color: var(--text-primary-color);
	opacity: 0.9;
}

.button_type_secondary:active {
	color: var(--text-primary-color);
}

.button_size_large {
	font-family: 'Jet Brains Mono';
	font-size: 16px;
	line-height: 1.5;
	font-weight: normal;
	padding: 20px 40px;
}
.button_size_medium {
	font-family: 'Jet Brains Mono';
	font-size: 16px;
	line-height: 1.5;
	font-weight: normal;
	padding: 16px 40px;
}
.button_size_small {
	font-family: 'JetBrains Mono';
	font-size: 14px;
	line-height: 1.43;
	font-weight: normal;
	padding: 10px 32px;
}
.button_type_primary[disabled] {
	color: var(--text-inactive-color);
	background: var(--colors-button-inactive);
	pointer-events: none;
}
.button_type_secondary[disabled] {
	color: var(--text-inactive-color);
	pointer-events: none;
}

.constructor-element {
    padding: 16px 24px;
    display: inline-block;
    border-radius: var(--common-border-radius-s);
    background: var(--background-element);
    max-width: 536px;
    width: 100%;
    min-height: 80px;
    font-family: 'Jet Brains Mono';
    font-size: 16px;
    line-height: 24px;
    color: #F2F2F3;
    flex-grow: 1;
    box-sizing: border-box;
}

.constructor-element_pos_top {
    border-radius: var(--top-constructor-item-border-radius);
}

.constructor-element_pos_bottom {
    border-radius: var(--bottom-constructor-item-border-radius);
}

.constructor-element__row {
    display: flex;
    align-items: center;
}

.constructor-element__image {
    width: 80px;
    height: 40px;
    margin-right: 20px;
}

.constructor-element__text {
    margin-right: 20px;
    display: inline-flex;
    align-items: center;
    line-height: 24px;
    text-align: left;
    flex-grow: 1;
}

.constructor-element__price {
    display: inline-flex;
    align-items: center;
    justify-content: flex-end;
    text-align: right;
    font-family: 'Iceland';
    font-style: normal;
    font-weight: normal;
    font-size: 28px;
    line-height: 24px;
}

.constructor-element__price svg {
    margin-left: 9px;
}

.constructor-element__action {
    margin-left: 20px;
    display: inline-flex;
    align-items: center;
    cursor: pointer;
}

.tab {
  cursor: pointer;
  background-color: var(--background-alt);
  min-width: 200px;
  text-align: center;
  box-shadow: inset 0px -1px 0px #2F2F37;
  color: var(--text-inactive-color);
  transition: var(--common-transition);
  box-sizing: border-box;
}

.tab:hover {
  color: var(--text-primary-color);
}

.tab_type_current {
  box-shadow: inset 0px -2px 0px #4C4CFF;
  pointer-events: none;
  color: var(--text-primary-color);
}

.input {
  display: flex;
  cursor: text;
  align-items: center;
  justify-content: space-between;
  box-sizing: border-box;
  background-color: var(--background-element);
  border: 2px solid var(--background-element);
  border-radius: var(--common-border-radius-s);
  transition: 0.3s;
  position: relative;
}

